.social-container {
    padding: 0px 0px;
        position:fixed;
        bottom:0;
        right:0;
    background-color: whitesmoke;
    border-radius: 10px;
    margin-bottom: 10px;
    margin-right: 10px;
}

a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
}

a.social:hover {
    transform: translateY(-5px);
    color: #70a5e9;
}

a.discord:hover {
    transform: translateY(-5px);
    color: #8b9cdd;
}

a.twitter {
    color: #49a1eb;
    margin-top: 15px;
    padding: 5px;
    margin-bottom: 15px;
    background-color: white;
    border-radius: 30px;
}

a.discord {
    color: #7289d9;
    padding: 5px;
    background-color: white;
    border-radius: 30px;
}